"use client";

import React, { useEffect, useState } from "react";

interface Category {
  id: string;
  name: string;
}

const categoryTranslations: Record<string, string> = {
  strategy: "Estrategia",
  family: "Familiar",
  party: "Fiesta",
  "card game": "Juego de Cartas",
  cooperative: "Cooperativo",
  educational: "Educativo",
  adventure: "Aventura",
  fantasy: "Fantasía",
  "war game": "Juego de Guerra",
  abstract: "Abstracto",
};

export default function CatalogoPage() {
  const [categories, setCategories] = useState<Category[]>([]);

  useEffect(() => {
    async function fetchCategories() {
      try {
        const res = await fetch("/api/categorizer");
        const data = await res.json();
        if (data.categories) {
          const translated = data.categories.map((cat: string) => ({
            id: cat,
            name: categoryTranslations[cat.toLowerCase()] || cat,
          }));
          setCategories(translated);
        }
      } catch (error) {
        console.error("Error fetching categories:", error);
      }
    }
    fetchCategories();
  }, []);

  return (
    <div>
      <h1>Categorías</h1>
      <ul>
        {categories.map((cat) => (
          <li key={cat.id}>{cat.name}</li>
        ))}
      </ul>
    </div>
  );
}
